.container {
    max-width: 600px;
    margin: 2rem auto;
    padding: 2rem;
    background: #f8f9fa;
    border-radius: 12px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  }
  
  .title {
    font-size: 2rem;
    color: #1a237e;
    margin-bottom: 1.5rem;
    text-align: center;
  }
  
  .weatherInfo {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
  }
  
  .temperature {
    font-size: 1.5rem;
    color: #0d47a1;
    font-weight: 500;
  }
  
  .feelsLike {
    color: #546e7a;
    font-size: 1rem;
  }
  
  .weatherIcon {
    margin: 1rem 0;
    background: #e3f2fd;
    border-radius: 50%;
    padding: 0.5rem;
  }